export default {
  // 食物选择界面
  name: 'DietSelect',

  data() {
    return {
      InputBottom: 0,
      searchValue: '',
      tabList: [],
      foodList: [],
      currentFood: {
        name: '', //食物名称
        calories: '', //热量、卡路里
        weight: ''
      },
      tabCur: 0,
      mainCur: 0,
      verticalNavTop: 0,
      load: true,
      modalName: ''
    };
  },
  methods: {
    TabSelect(e,item) {
      console.log(`你选择了${item.label}`)
      this.foodList = []
      this.tabCur = e.currentTarget.dataset.id;
      this.mainCur = e.currentTarget.dataset.id;
      this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50;
      this.getFoodList()
    },
    openSelectedBox() {
      // 弹出已选
    },
    openChooseBox(e, item) {
      // 弹出选择框
      console.log(e, item);
      this.currentFood.name = item.label;
      this.currentFood.calories = item.calories
      this.modalName = e.currentTarget.dataset.target;
    },
    hideModal(e) {
      this.modalName = null;
    },
    VerticalMain(e) {
