    //   }
    },
    getTabList() {
      let list = ['水果', '蔬菜', '谷物', '肉类', '蛋类', '豆类', '乳制品', '甜品', '水饮', '中式', '酱料'];
      list = list.map(item => {
        return {
          label: item
        };
      });
      this.tabList = list;
    },
    getFoodList() {
      let list = [
        {
          label: '橘子',
          unit: '个',
          weight: '200克',
          calories:'30千卡'
        },
        {
          label: '香蕉',
          unit: '个',
          weight: '200克',
          calories:'30千卡'
        },
        {
          label: '苹果',
          unit: '个',
          weight: '200克',
          calories:'30千卡'
        },
        {
          label: '猕猴桃',
          unit: '个',
