          calories:'30千卡'
        },
        {
          label: '樱桃',
          unit: '个',
          weight: '200克',
          calories:'30千卡'
        },
        {
          label: '榴莲',
          unit: '个',
          weight: '200克',
          calories:'30千卡'
        }
      ];
      this.foodList = list;
    },
    clearText() {
      this.searchValue = '';
    },
    InputFocus(e) {
      this.InputBottom = e.detail.height;
      console.log(e.detail, 'Focus');
    },
    InputBlur(e) {
      this.InputBottom = 0;
      console.log(e.detail.value, 'blur');
    }
  },
  onLoad() {
    uni.showLoading({
      title: '加载中...',
      mask: true
    });
    this.getTabList(); //获取食物分类tab
    this.getFoodList();
  },
  onReady() {
    uni.hideLoading();
  }
};
